
.skill-coin-description{
  color:white;
  font-size: 25px;
  margin: 0;
  padding: 0;
  font-weight: 500;
}
.skill-coin-description .coin{
  margin-right: 5px;
}
.condition{
  margin-right: 5px;
}
.condition--onhit{
  color: green;
}
.condition--clashwin{
  color: orange;
}
.condition--combatstart{
  color: green;
}
.condition--clashLose{
  color: red;
}
.condition--onuse{
  color: blue;
}
.condition--onkill{
  color: green;
}
.condition--onevade{
  color: green;
}
.condition--headshit{
  color: rgb(2, 185, 2);
}
.skill-coin-description img{
  width: 25px;
  height: 25px;
  object-position: center;
  object-fit: contain;
  margin-bottom: -5px;
}
.status-name{
  text-decoration: underline;
  color:rgb(168, 0, 0);
}
.status-name--sinking{
  color:rgb(0, 102, 255);
}
.status-name--fragile{
  color:rgb(149, 0, 255);
}
.status-name--paralyze{
  color:rgb(255, 208, 0);
}
.status-name--haste{
  color:orange;
}
.status-name--fragile{
  color:rgb(149, 0, 255);
}