.entityFullInfo-skill{
    width: 90%;
    height: fit-content;
    display: flex;
    flex-direction: row;
    position: relative;
    background-color: #25272D;
    border-radius: 8px;
    padding: 10px;
    gap:10px;
}
.entityFullInfo-skill-l{
    padding-top:30px;
    padding-left:50px;
    position: relative;
    height: 180px;

}

.entityFullInfo-skill-l img {
    width: 160px;
    height: 160px;
    object-fit: contain;
}
.entityFullInfo-skill-r{
    width: 80%;
    display: flex;
    flex-direction: column;
}
.skill-sin-frame{
    height: 8px;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
}
.skill-coinGrowth{
    font-size: 30px;
    color:#ebbb84;
    position: absolute;
    top:-5px;
    transform: translateX(-50%) ;
    left:50%;
    text-decoration: underline;
    cursor: pointer;
    -webkit-text-stroke: 1px black;
    text-decoration: underline  #ebbb84;
    text-decoration-thickness: 2px;
}
.tooltip-container:hover .entityFullInfo-tooltip{
    opacity: 1;
    transform: translate(-50%,-100%) ;
}
.skill-baseCoin{
    font-size: 150px;
    color:#bf8c52;
    -webkit-text-stroke: 4px black;
    position: absolute;
    left:0;
    top:0;
    cursor: pointer;
    box-decoration-break: clone;
    text-decoration: underline  #bf8c52;
    text-decoration-thickness: 5px;
    text-underline-offset: 5px;
}
.skill-baseCoin:hover .entityFullInfo-tooltip{
    transform: translate(-50%,30%) !important;
}
.entityFullInfo-coins{
    display: flex;
    flex-direction: row;
}   
.entityFullInfo-coins img{
    width: 30px;
    height: 30px;
}
.skill-name{
    color:white;
    width: fit-content;
    border-radius: 2px;
    padding: 2px;
    padding: 5px 10px;
    font-size: 20px;
    -webkit-text-stroke: 0.1px black;
}
.entityFullInfo-skill-index{
    color: white;
    font-size: 20px;
    position: absolute;
    right: 10px;
    top:10px;
}
.skill-atk{
    color:white;
    display: flex;
    align-items: center;
    position: relative;
    gap:2px;
    cursor:pointer;
    font-size: 20px;
  font-weight: 500;


}
.skill-atk img{
    width: 30px;
    height: 30px;
    object-fit: contain;
}
.skill-weight{
    color:rgb(241, 211, 77);
    display: flex;
    align-items: center;
    position: relative;
    gap:5px;
    cursor:pointer;
    font-size: 20px;
    font-weight: 500;

}
.skill-weight div{
    width: 14px;
    height: 14px;
    background-color: rgb(241, 211, 77);
}